import * as actionTypes from './cms.actiontype';
import CmsService from '../../services/CmsService';

export const addcms = (formdata) => {
    return async dispatch => {
        function addCmsFail(message) {
            dispatch({ 
                type: actionTypes.ADD_CMS_FAIL,
                message:message
            });
            return {success:false,error:message}
        }
        function addCmsSuccess(message) {
            dispatch({ 
                type:actionTypes.ADD_CMS_SUCCESS,
                message:message
            })
            return {success:true,message:message};
        }
        try {
            dispatch({ 
                type: actionTypes.ADD_CMS_START
            });
            let cmsservice = new CmsService();
            const data = await cmsservice.addCms(formdata).then((res)=>{
                if(res.data.success){
                    return addCmsSuccess(res.data.message);
                }else{
                    return addCmsFail(res.data.message); 
                }
            });
            return data;
        } catch (error) {
            if(error.response){
                return addCmsFail(error.response.data.message);
            }else{
                return addCmsFail("Something went wrong!");
            }
        }
      }
}

export const getcms = () => {
    return async dispatch => {
        function getCmsFail(message) {
            dispatch({ 
                type: actionTypes.GET_CMS_FAIL,
                message:message
            });
            return {success:false,error:message}
        }
        function getCmsSuccess(message, data) {
            dispatch({ 
                type:actionTypes.GET_CMS_SUCCESS,
                message:message,
                cms:data
            })
            return {success:true,message:message};
        }
        try {
            dispatch({ 
                type: actionTypes.GET_CMS_START
            });
            let cmsservice = new CmsService();
            const data = await cmsservice.getCms().then((res)=>{
                if(res.data.success){
                    return getCmsSuccess(res.data.message, res.data.data);
                }else{
                    return getCmsFail(res.data.message); 
                }
            });
            return data;
        } catch (error) {
            if(error.response){
                return getCmsFail(error.response.data.message);
            }else{
                return getCmsFail("Something went wrong!");
            }
        }
    }
}

export const getCmsById = (cmsId) => {
    return async dispatch => {
        function getCmsByIdFail(message){
            dispatch({ 
                type: actionTypes.GET_CMS_BY_ID_FAIL,
                message:message
            });
            return {success:false,error:message}
        }
        function getCmsByIdSuccess(message, data){
            dispatch({ 
                type:actionTypes.GET_CMS_BY_ID_SUCCESS,
                message:message,
                cmsdetail:data
            })
            return {success:true,message:message};
        }
        try{
            dispatch({ 
                type: actionTypes.GET_CMS_BY_ID_START
            });
            let cmsservice = new CmsService();
            const data = await cmsservice.getCmsById(cmsId).then((res)=>{
                if(res.data.success){
                    return getCmsByIdSuccess(res.data.message, res.data.data);
                }else{
                    return getCmsByIdFail(res.data.message); 
                }
            });
            return data;
        }catch(error){
            if(error.response){
                return getCmsByIdFail(error.response.data.message);
            }else{
                return getCmsByIdFail("Something went wrong!");
            }
        }
    }
}

