import React,{Component} from 'react';
import { ReactFormBuilder } from 'react-form-builder2';
import { ReactFormGenerator } from 'react-form-builder2';
import withAuth from '../../hoc/withAuthentication';
import 'react-form-builder2/dist/app.css';
import { connect } from 'react-redux';
import * as actions from '../../store/actions/cms/cms.action';
import { store } from 'react-notifications-component';
import 'react-notifications-component/dist/theme.css'
import 'animate.css'

class AddCms extends Component {
   
    constructor(props){
        super(props)
        this.state = {
            formbuilder:[],
            preview:false
        }
    }

    formData = async(data) => {
         this.setState({
            formbuilder:data.task_data
         })
    }

    saveForm = async() => {
        if(this.state.formbuilder.length>0){
            const self = this;
            const addcms = await this.props.addcms(this.state.formbuilder);
            if(addcms.success){
                store.addNotification({
                    title: 'Success',
                    message: addcms.message,
                    type: 'default',
                    insert: 'top',
                    container: 'top-right',
                    animationIn: ['animated', 'fadeIn'],
                    animationOut: ['animated', 'fadeOut'],
                    dismiss: {
                      duration: 2000,
                      onScreen: true,
                    },
                }) 
                setTimeout(()=>{
                    self.props.history.push("/cms")
                }, 1000)
            }
    
            if(!addcms.success){
                store.addNotification({
                    title: 'Error',
                    message: addcms.error,
                    type: 'danger',
                    insert: 'top',
                    container: 'top-right',
                    animationIn: ['animated', 'fadeIn'],
                    animationOut: ['animated', 'fadeOut'],
                    dismiss: {
                      duration: 2000,
                      onScreen: true,
                    },
                })
            }
        }else{
            store.addNotification({
                title: 'Error',
                message: "Please add an element to add form",
                type: 'danger',
                insert: 'top',
                container: 'top-right',
                animationIn: ['animated', 'fadeIn'],
                animationOut: ['animated', 'fadeOut'],
                dismiss: {
                  duration: 2000,
                  onScreen: true,
                },
            })
        }
        
    }

    previewForm = async() => {
         if(this.state.formbuilder.length>0){
            this.setState({preview:true})
         }else{
            store.addNotification({
                title: 'Error',
                message: "Please add an element to preview",
                type: 'danger',
                insert: 'top',
                container: 'top-right',
                animationIn: ['animated', 'fadeIn'],
                animationOut: ['animated', 'fadeOut'],
                dismiss: {
                  duration: 2000,
                  onScreen: true,
                },
            })
         }
    }


    render(){
        return(
            <div style={{marginTop:40, marginBottom:40, minHeight:"100vh"}}>
                 {!this.state.preview?(
                 <ReactFormBuilder 
                    onPost={(data)=>{this.formData(data)}}
                    data={this.state.formbuilder}
                 />):null}
                 {this.state.preview?(
                     <div className="card mr-auto ml-auto mb-5">
                         <div className="card-body">
                            <ReactFormGenerator data={this.state.formbuilder} />
                         </div>
                     </div>
                    
                 ):null} 
                 <div className="form-group d-flex">
                     <button onClick={()=>{this.saveForm()}} className="btn btn-primary btn-small">Save Form {this.props.loading?(<i class="fa fa-circle-o-notch fa-spin" style={{fontSize:"24px"}}></i>):null}</button>
                     {!this.state.preview?(<button onClick={()=>{this.previewForm()}} className="btn btn-primary btn-small ml-3">Preview Form</button>):null}
                     {this.state.preview?(<button onClick={()=>{this.setState({preview:false})}} className="btn btn-primary btn-small ml-3">Continue Editing</button>):null}
                 </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        loading: state.documentReducer.loading,
        error: state.documentReducer.error,
        message:state.documentReducer.message
    };
}

const mapDispatchToProps = dispatch => {
    return {
        addcms:(formdata)=>dispatch(actions.addcms(formdata))
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(withAuth(AddCms))