import API from "./Api";

class CmsService {
     addCms = (data) => {
        let auth = JSON.parse(localStorage.getItem('authUser'));
        const headers = {
           'Authorization': auth.token
        }
        let params = {
            formdata:data
        }
        return API.post("/cms",params,{headers: headers})
     }

     updateCms = (cmsid,data) => {
        let auth = JSON.parse(localStorage.getItem('authUser'));
        const headers = {
            'Authorization': auth.token
         }
         let params = {
            formdata:data
         }
         return API.put(`/cms/${cmsid}`,params,{headers: headers})
     }

     getCms = () => {
       let auth = JSON.parse(localStorage.getItem('authUser'));
       const headers = {
           'Authorization': auth.token
       }
       return API.get(`/allcms`,{headers: headers})
    }

    getCmsById = (cmsid) => {
        let auth = JSON.parse(localStorage.getItem('authUser'));
        const headers = {
            'Authorization': auth.token
        }
        return API.get(`/getcmsbyid/${cmsid}`,{headers: headers})
     }

     deleteCms = (cmsid) => {
        let auth = JSON.parse(localStorage.getItem('authUser'));
        const headers = {
            'Authorization': auth.token
        }
        return API.delete(`/cms/${cmsid}`,{headers: headers})
     }
}

export default CmsService;